import { ethers } from "ethers";
import { formatFixed, parseFixed } from '@ethersproject/bignumber'

// const evalInScope = async (js, contextAsScope) => {
//     return async () => { with(this) { return await eval(js); }; }.call(contextAsScope);
// }

const utils = {
  formatFixed,
  parseFixed,
}

const prefix = "async (ethers, utils, fetch, history, location, localStorage, navigator, window) => {"
const subfix = "}"

const __context__ = {
  eval: null,
  fetch: null,
  history: null,
  import: null,
  location: null,
  localStorage: null,
  navigator: null,
  window: null,
  ethers: ethers,
  utils: utils,
}

export const compileCode = (code) => {
  let finalCode = prefix + code + subfix;
  try {
    return eval(finalCode);
  } catch (e) {
    console.log(e);
    return undefined;
  }
}

export const runCode = async (code) => {
  let f = compileCode(code);
  try {
    return await f(ethers, utils);
  } catch (e) {
    console.log(e);
    return undefined;
  }
}
