import {useState, useEffect} from "react";
import {runCode} from "./executor";

import { Button } from 'primereact/button';
import { DataTable } from 'primereact/datatable';
import { Column } from 'primereact/column';

const cardClass = (width) => {
  return "basis-full lg:basis-1/4 lg:flex-initial text-slate-800 bg-red-100 px-2 py-1 m-2 max-w-full"
} 

export function TextCard({config}) {
  let [text, setText] = useState("");
  let [loading, setLoading] = useState(false);
  useEffect(() => {
    let run = async () => {
      setLoading(true);
      setText(await runCode(config.code));
      setLoading(false);
    }
    run();
  }, [config.code])

  const renderBody = () => {
    return <>{text}</>  
  }

  return (
    <>
      {
        loading ? <i className="pi pi-spin pi-spinner"></i> : renderBody()
      }
    </>
  )
}

export function TableCard({config}) {
  let [data, setData] = useState([]);
  let [loading, setLoading] = useState(false);
  useEffect(() => {
    let run = async () => {
      setLoading(true);
      let {headers, values} = await runCode(config.code);
      let x = headers.map((item, idx) => {
        let col = [item];
        for (const val of values) {
          col.push(val[idx]);
        }
        return col;
      });
      setData(x);
      setLoading(false);
    }
    run();
  }, [config.code])

  const renderCol = (li) => {
    return <div className="flex flex-col">
      {
        li.map((item, idx) => {
          return <div key={idx} className={"border-b border-slate-900 px-2 " + (idx === 0 ? "font-bold" : "")}>{item}</div>
        })
      }
    </div>
  }

  const renderBody = () => {
    return <div className="flex justify-between gap-0 max-h-full overflow-scroll  ">
      {
        data.map((item) => {
          return renderCol(item);
        })
      }
    </div>
  }

  return (
    <>
      {
        loading ? <i className="pi pi-spin pi-spinner"></i> : renderBody()
      }
    </>
  )
}
