import { useEffect, useState } from 'react'

import {metaMask, hooks} from "./connectors/metamask.js";
import ConnectBtn from "./widgets/ConnectBtn.jsx";
import {TextCard, TableCard} from "./Card.jsx";
import { Sidebar } from 'primereact/sidebar';

import { InputText } from 'primereact/inputtext';
import { InputTextarea } from 'primereact/inputtextarea';
import { Dropdown } from 'primereact/dropdown';
import { Button } from 'primereact/button';

import {compileCode} from "./executor";

const originalCards = [
  {
    title: "Balance",
    code: `
const provider = new ethers.providers.JsonRpcProvider("https://mainnet.infura.io/v3/9aa3d95b3bc440fa88ea12eaa4456161");
let x = await provider.getBalance("0xAb5801a7D398351b8bE11C439e05C5B3259aeC9B");
return utils.formatFixed(x, 18);
    `,
    display: "text",
  },
  {
    title: "Table test",
    code: `
return {
  "headers": ["foo", "bar", "address"],
  "values": [
    ["foo1", "bar1", "0xAb5801a7D398351b8bE11C439e05C5B3259aeC9B"],
    ["foo2", "bar2", "0xAb5801a7D398351b8bE11C439e05C5B3259aeC9B"],
  ]
}
    `,
    display: "table",
  },
]

function useLocalStorage(key, initialValue) {
  const [storedValue, setStoredValue] = useState(() => {
    if (typeof window === "undefined") {
      return initialValue;
    }
    try {
      const item = window.localStorage.getItem(key);
      return item ? JSON.parse(item) : initialValue;
    } catch (error) {
      return initialValue;
    }
  });
  const setValue = (value) => {
    try {
      const valueToStore =
        value instanceof Function ? value(storedValue) : value;
      setStoredValue(valueToStore);
      if (typeof window !== "undefined") {
        window.localStorage.setItem(key, JSON.stringify(valueToStore));
      }
    } catch (error) {
      console.log(error);
    }
  };
  return [storedValue, setValue];
}


function App() {
  const [cards, setCards] = useLocalStorage("uliPanel__cards", originalCards);
  const [sideBarVisiable, setSideBarVisiable] = useState(false);
  const [curItem, setCurItem] = useState(null);

  const renderCardBody = (c, idx) => {
    if (c.display === "table") {
      return <TableCard config={c} key={idx}/>
    }
    return <TextCard config={c} key={idx}/>
  }

  const showSideBar = (config, idx) => {
    let item = {
      title: "",
      code: "",
      display: "text",
      idx: idx,
    }
    if (config) {
      item.title = config.title;
      item.code = config.code;
      item.display = config.display;
      item.idx = idx;
    }
    setCurItem(item);
    setSideBarVisiable(true);
  } 

  const renderCard = (config, idx) => {
    return <div className="basis-full lg:basis-1/4 text-slate-800 bg-red-100 px-2 py-1 m-2 max-w-full" key={idx}>
      <div className="flex h-8 border-b border-red-200 items-center font-bold justify-between">
        <div>{config.title}</div>
        <div>
          <i className="pi pi-cog w-4 h-4 cursor-pointer" onClick={() => {showSideBar(config, idx)}}></i> 
        </div>
      </div>
      <div className="self-center h-40 flex items-center justify-center">
      {
        renderCardBody(config, idx)
      }
      </div>
    </div>
  }

  const addOrUpdateCard = () => {
    if (curItem.title.length === 0) {
      alert("title format error");
      return;
    }
    let result = compileCode(curItem.code);
    if (result === undefined) {
      alert("code error");
      return;
    }
    if (curItem.idx < 0) {
      setCards(state => [...state, curItem]);
    } else {
      setCards(state => {
        const newArr = [...state];
        newArr[curItem.idx] = curItem;
        return newArr;
      });
    }
    setSideBarVisiable(false);
  }

  const deleteCard = () => {
    if (curItem.idx < 0) {
      return;
    } 
    setCards(state => {
      const newArr = [...state];
      newArr.splice(curItem.idx, 1);
      return newArr;
    });
    setSideBarVisiable(false);
  }

  const displayOpitons = [
    {label: "text", value: "text"},
    {label: "table", value: "table"},
  ]


  const renderSideBar = () => {
    return <div>
      <span className="p-float-label mt-10">
        <InputText id="title" value={curItem.title} onChange={(e) => {setCurItem(prevState=>({...prevState, title: e.target.value}))}} className="w-full"/>
        <label htmlFor="title">Title:</label>
      </span>
      <div className="p-float-label mt-10">
        <Dropdown value={curItem.display} options={displayOpitons} onChange={(e) => {setCurItem(prevState=>({...prevState, title: e.value}))}} className="w-full"/>
        <label htmlFor="title">Display:</label>
      </div>
      <span className="p-float-label mt-10">
        <InputTextarea rows={10} value={curItem.code} onChange={(e) => {setCurItem(prevState=>({...prevState, code: e.target.value}))}} className="w-full text-xs"/>
        <label htmlFor="title">Code:</label>
      </span>
      <div className="flex justify-around mt-4">
      <Button label="Save" icon="pi pi-check" className="p-button-outlined" onClick={() => {addOrUpdateCard()}}/>
      {
        curItem.idx >= 0 ? <Button label="Delete" icon="pi pi-times" className="p-button-danger p-button-outlined" onClick={() => {deleteCard()}}/> : null
      }
      </div>
    </div>
  }

  return (
    <div>
      <div className="px-4 md:px-6 bg-gray-50 min-h-screen text-sm">
      <div className="flex flex-row-reverse">
        <i className="pi pi-plus w-4 h-4 cursor-pointer" onClick={() => {showSideBar(null, -1)}}></i> 
      </div>
        <div className="pt-2 flex flex-row flex-wrap ">
          {
            cards.map((c, idx) => {
              return renderCard(c, idx);
            })
          }
        </div>
      </div>
      <Sidebar visible={sideBarVisiable} onHide={() => setSideBarVisiable(false)} className="p-sidebar-md" position="right">
        {curItem ? renderSideBar() : null}
      </Sidebar>
    </div>
  );
}

export default App;
